import { Component, Host, Prop, h } from '@stencil/core';

/**
 * The container component.
 */
@Component({
  tag: 'bs-container',
  styleUrl: 'container.scss',
  shadow: false,
})
export class BsContainer {
  /** Props */
  /**
   * Should the content be centered with a max width on mobile?
   */
  @Prop() mobileFullWidth?: boolean = false;

  /**
   * Should the content be centered with a max width on desktop?
   */
  @Prop() contained?: boolean = false;

  render() {
    return (
      <Host
        class={`bs-container ${this.mobileFullWidth ? 'is-mobile-full-width' : ''} ${
          this.contained ? 'is-contained' : ''
        }`}
      >
        <slot />
      </Host>
    );
  }
}
